import { Component } from '@angular/core';
import { ApicallingService } from '../apicalling.service';
import { NzModalService } from 'ng-zorro-antd/modal';
import { HttpClient } from '@angular/common/http';

@Component({
  selector: 'app-extras',
  templateUrl: './extras.component.html',
  styleUrls: ['./extras.component.css']
})
export class ExtrasComponent {
  isVisible:boolean=false;
  Name : string="";
  Price : number = 0;
  dbResult : any;
  search:string="";
  lstExtras : any= [];

  constructor(private apiCall : ApicallingService,private modal: NzModalService,private http:HttpClient) { }

  ngOnInit(): void {
    debugger;
    this.view('');
  }

  view(e:any){
    debugger;
    this.apiCall.DBCalling('View',"Extras","","","",this.search).subscribe(res=>{
      debugger;
      this.dbResult = res;
      this.lstExtras = this.dbResult[0];
    })
  }

  
  AddExtras(){
    debugger;
    this.isVisible=true;
  }

  Id : number = 0;
  operation : string ="";
  submit : boolean = false;
  msg : string ="";
  Exist : boolean=false;

  saveExtras(){
    debugger;
    this.submit = true;
    if(this.Name !='' ){
      debugger;
      var data = ({'Id':this.Id,'Name':this.Name,'Price':this.Price});
      var jsonData =  JSON.stringify(data);
      if(this.Id == 0){
        this.operation = "Insert";
      }else{
        this.operation = "Update";
      }
      this.apiCall.DBCalling(this.operation,"Extras",jsonData,"","","").subscribe(res=>{
        debugger;
        this.dbResult = res;
        if(this.dbResult[0][0].DBresult > 0){
            if(this.Id == 0){
              this.msg = 'Saved';
            }else{
              this.msg = 'Updated';
            }
            this.isVisible = false;
            this.Reset();
            this.success();
            this.view('');
        }else if(this.dbResult[0][0].DBresult == -1){
          debugger;
            this.Exist=true
        }
      });
    }
  }

  Edit(e:any){
    this.Id = e.Id;
    this.Name = e.Name;
    this.Price = e.Price;
    this.isVisible = true;
  }

  Delete(e:any){
    this.modal.confirm({
      nzTitle: 'Are you sure?',
      nzContent: '<b style="color: red;">You wont be able to revert this!</b>',
      nzOkText: 'Yes',
      nzOkType: 'primary',
      nzOkDanger: true,
      nzOnOk: () => this.apiCall.DBCalling('Delete',"Extras",e,"","","").subscribe(res=>{
        debugger;
        this.dbResult = res;
        if(this.dbResult[0][0].DBresult > 0){
            this.msg = 'Deleted';
            this.success();
            this.isVisible = false;
            this.view('');
        }
      }),
      nzCancelText: 'No',
      nzOnCancel: () => console.log('Cancel')
    });

  }

  success(): void {
    this.modal.success({
      nzTitle: 'Success',
      nzContent: 'Extras ' + this.msg + ' Successfully!'
    });
  }

  Reset(){
    this.Id = 0;
    this.Name = "";
    this.Price = 0;
    this.submit = false;
  }

  handleCancel(){
    debugger;
    this.Reset();
    this.isVisible=false;
    this.submit=false;
  }
}
