import { Component } from '@angular/core';
import { ApicallingService } from './apicalling.service';
import { HttpClient } from '@angular/common/http';
import { Router } from '@angular/router';
import { Store } from '@ngrx/store';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  isCollapsed = false;
  dbResult: any;
  lstAdmin: any = [];
  Logo : string =""
  imagePath: string = "";

  constructor(private apiCall: ApicallingService, private http: HttpClient,private router:Router,
    private store:Store<any>){
    this.imagePath = this.apiCall.imagePath;

  }


  ngOnInit(){
    debugger;
    if (this.store.source) {
      this.store.source.subscribe((data: any) => {
        debugger;
        const result2 = data['xeato_admin'].filter((x: any) => x.ViewName === 'loginusers');
          if(result2.length > 0){
            if(result2[0].Id!='' && result2[0].Id!=0 && result2[0].Id!=null){
              
            }else{
              this.router.navigateByUrl('Login');
            }
          }
      });
    }

    this.view();
  }
  
  view() {
    debugger;
    this.apiCall.DBCalling('View', "Admin", "", "", "", "").subscribe(res => {
      debugger;
      this.dbResult = res;
      this.lstAdmin = this.dbResult[0][0];
      this.Logo = this.imagePath + this.lstAdmin.Logo;

      const link = document.querySelector("link[rel*='icon']") as HTMLLinkElement || document.createElement('link');
      link.type = 'image/x-icon';
      link.rel = 'shortcut icon';
      link.href =  this.Logo;
      document.getElementsByTagName('head')[0].appendChild(link);
      
    })
  }
  

}
