import { Component } from '@angular/core';
import { ApicallingService } from '../apicalling.service';
import { NzModalService } from 'ng-zorro-antd/modal';
import { HttpClient } from '@angular/common/http';

@Component({
  selector: 'app-profile',
  templateUrl: './profile.component.html',
  styleUrls: ['./profile.component.css']
})
export class ProfileComponent {

  dbResult: any;
  lstAdmin: any = [];
  imagePath: string = "";
  imageUpload: string = "";
  lstCurrency: any = [];
  submit: boolean = false;
  status: boolean = false;
  Logo: any;
  logo1: string = "";

  restaurant: any;
  restaurant1: string = "";

  Image: string = "";
  Imagetype: string = "";
  restaurantImage: string = "";
  Id : number = 0;
  operation : string = "";
  msg : string = "";
  allChecked = false;
  DeliveryCharges : number = 1;
  Tax : number = 0;

  adminOptions = [
    { label: 'Cod', value: 'Cod', checked: false },
    { label: 'Online', value: 'Online', checked: false },
    { label: 'Pickup', value: 'Pickup', checked: false },
    { label: 'Delivery', value: 'Delivery', checked: false }
  ];

  constructor(private apiCall: ApicallingService, private modal: NzModalService, private http: HttpClient) {
    this.imagePath = this.apiCall.imagePath;
    this.imageUpload = this.apiCall.ImageUpload;
  }

  ngOnInit() {
    this.view();
    this.viewCurrency();
  }

  view() {
    debugger;
    this.apiCall.DBCalling('View', "Admin", "", "", "", "").subscribe(res => {
      debugger;
      this.dbResult = res;
      this.lstAdmin = this.dbResult[0][0];
      this.status = true;
      this.Logo = this.imagePath + this.lstAdmin.Logo;
      this.restaurant = this.imagePath + this.lstAdmin.Image;
      this.logo1 = this.imagePath + this.lstAdmin.Logo;
      this.restaurant1 = this.imagePath + this.lstAdmin.Image;

      if(this.lstAdmin.Cod == 1){
        debugger;
        this.adminOptions[0].checked = true;
      }
      if(this.lstAdmin.Online == 1){
        debugger;
        this.adminOptions[1].checked = true;
      }
      if(this.lstAdmin.Pickup == 1){
        debugger;
        this.adminOptions[2].checked = true;
      }
      if(this.lstAdmin.Delivery == 1){
        debugger;
        this.adminOptions[3].checked = true;
      }
    })
  }

  viewCurrency() {
    debugger;
    this.apiCall.DBCalling('View', "Currency", "", "", "", "").subscribe(res => {
      debugger;
      this.dbResult = res;
      this.lstCurrency = this.dbResult[0];
    })
  }



  save(){
    debugger;

    


    // this.lstAdmin.Cod = (this.adminOptions.filter(x=>x.label === 'Cod' && x.checked === true)) ? 1 : 0;
    // this.lstAdmin.Online = this.adminOptions.filter(x=>x.label === 'Online' && x.checked === true) ? 1 : 0;
    // this.lstAdmin.Delivery = this.adminOptions.filter(x=>x.label === 'Delivery' && x.checked === true) ? 1 : 0;
    // this.lstAdmin.Pickup = this.adminOptions.filter(x=>x.label === 'Pickup' && x.checked === true) ? 1 : 0;
    debugger;
    this.submit = true;
    if(this.lstAdmin.FirstName !='' && this.lstAdmin.Lastname !='' && this.lstAdmin.Email !='' && this.lstAdmin.Phone !='' && this.lstAdmin.UserName !='' 
    && this.lstAdmin.Password !='' && this.lstAdmin.Image !='' && this.lstAdmin.RestaurantName !='' && this.lstAdmin.RestaurantEmail !='' && this.lstAdmin.Address !=''
    && this.lstAdmin.Timings !='' && this.lstAdmin.Workingdays !='' && this.lstAdmin.Logo !='' && this.lstAdmin.Currency !='' && this.lstAdmin.Currency !=null){
      debugger;

      if(this.Image!=''){
        this.lstAdmin.Logo = this.Image
      }
      if(this.restaurantImage !=''){
        this.lstAdmin.Image = this.restaurantImage;
      }
      var data = ({'Id':this.lstAdmin.Id,'FirstName':this.lstAdmin.FirstName,'Lastname':this.lstAdmin.Lastname,'Email':this.lstAdmin.Email,'Phone':this.lstAdmin.Phone
      ,'Fax':this.lstAdmin.Fax,'UserName':this.lstAdmin.UserName,'Password':this.lstAdmin.Password,'Image':this.lstAdmin.Image,'RestaurantName':this.lstAdmin.RestaurantName
      ,'RestaurantEmail':this.lstAdmin.RestaurantEmail,'Address':this.lstAdmin.Address,'Timings':this.lstAdmin.Timings,'Workingdays':this.lstAdmin.Workingdays
      ,'SupportNo':this.lstAdmin.SupportNo,'Logo':this.lstAdmin.Logo,'Currency':this.lstAdmin.currencyId
      ,'Pickup':this.lstAdmin.Pickup,'Delivery':this.lstAdmin.Delivery,'Cod':this.lstAdmin.Cod,'Online':this.lstAdmin.Online
      ,'DeliveryCharges':this.lstAdmin.DeliveryCharges,'Tax':this.lstAdmin.Tax,
      'Android':this.lstAdmin.Android,'Ios':this.lstAdmin.Ios,'Website':this.lstAdmin.Website});
      var jsonData =  JSON.stringify(data);

      if(this.lstAdmin.Delivery == 1 && this.lstAdmin.DeliveryCharges > 0){
        this.saveInDB(jsonData);
      }
      if(this.lstAdmin.Delivery != 1){
        this.saveInDB(jsonData);
      }
      
    }
  }

  saveInDB(jsonData:any){
    this.apiCall.DBCalling('Update',"Admin",jsonData,"","","").subscribe(res=>{
      debugger;
      this.dbResult = res;
      if(this.dbResult[0][0].DBresult > 0){
          if(this.lstAdmin.Id == 0){
            this.msg = 'Saved';
          }else{
            this.msg = 'Updated';
          }
     
          this.success();
          this.view();
      }
    });
  }

  success(): void {
    this.modal.success({
      nzTitle: 'Success',
      nzContent: 'Profile ' + this.msg + ' Successfully!'
    });
  }


  onFileChange(event: any, e: any) {
    debugger;
    this.status = false;
    this.Imagetype = e
    const file = event.target.files[0];
    this.status = event.target.files.length > 0 ? true : false

    var Imageurl = event.target.files[0];
    if (this.status == true) {
      const reader = new FileReader();
      reader.readAsDataURL(file);
      reader.onload = () => {

        if (e == 'logo') {
          this.Logo = reader.result;
          this.logo1 = this.Logo
        } else {
          this.restaurant = reader.result;
          this.logo1 = this.restaurant;
        }

        this.saveImage();

      }
    }
  }

  async saveImage() {
    if (this.Imagetype == 'logo') {
      try {
        let value = this.Image;
        this.Image = "";
        await this.http.post(this.imageUpload, { 'image': this.Logo }).subscribe((res) => {
          debugger;
          this.dbResult = res;
          if (res == null) {
            this.Image = value;
          } else {
            debugger;
            this.Image = this.dbResult[0][1];
          }
        })
      } catch (error) {
        console.log(error);
      }
    } else {
      try {
        let value = this.restaurantImage;
        this.restaurantImage = "";
        await this.http.post(this.imageUpload, { 'image': this.restaurant }).subscribe((res) => {
          debugger;
          this.dbResult = res;
          if (res == null) {
            this.restaurantImage = value;
          } else {
            debugger;
            this.restaurantImage = this.dbResult[0][1];
          }
        })
      } catch (error) {
        console.log(error);
      }
    }
  }

  updateSingleChecked(e:any): void {
    debugger;
    if (this.adminOptions.every(item => !item.checked)) {
    } else if (this.adminOptions.every(item => item.checked)) {
      this.allChecked = true;
    } else {
    }

    for(let i=0;i<this.adminOptions.length;i++){
      if(this.adminOptions[i].label == 'Cod' && this.adminOptions[i].checked == true){
        this.lstAdmin.Cod = 1;
      }else if(this.adminOptions[i].label == 'Cod' && this.adminOptions[i].checked == false){
        this.lstAdmin.Cod = 0;
      }

      if(this.adminOptions[i].label == 'Online' && this.adminOptions[i].checked == true){
        this.lstAdmin.Online = 1;
      }else if(this.adminOptions[i].label == 'Online' && this.adminOptions[i].checked == false){
        this.lstAdmin.Online = 0;
      }

      if(this.adminOptions[i].label == 'Delivery' && this.adminOptions[i].checked == true){
        this.lstAdmin.Delivery = 1;
      }else if(this.adminOptions[i].label == 'Delivery' && this.adminOptions[i].checked == false){
        this.lstAdmin.Delivery = 0;
      }

      if(this.adminOptions[i].label == 'Pickup' && this.adminOptions[i].checked == true){
        this.lstAdmin.Pickup = 1;
      }else if(this.adminOptions[i].label == 'Pickup' && this.adminOptions[i].checked == false){
        this.lstAdmin.Pickup = 0;
      }
     
    }

  }

  selectCurreny(d:any){
    debugger;
    // let data = this.lstCurrency.filter((x:any)=> x.Id ===d );
    this.lstAdmin.Currency = d
  }

}
