import { Component } from '@angular/core';
import { ApicallingService } from '../apicalling.service';
import { NzModalService } from 'ng-zorro-antd/modal';
import { HttpClient } from '@angular/common/http';
import { Router } from '@angular/router';
import { LoginUsers } from '../Store/LoginUsers';
import { Store } from '@ngrx/store';
import * as PageStore from "../Store/PageStore/Page.Actions";

@Component({
  selector: 'app-master-page',
  templateUrl: './master-page.component.html',
  styleUrls: ['./master-page.component.css']
})
export class MasterPageComponent {
  isCollapsed = false;
  dbResult : any=[];
  lstAdmin : any = [];
  LoginUsers : LoginUsers;
  constructor(private apiCall: ApicallingService, private modal: NzModalService, private http: HttpClient,private router:Router,
    private store:Store<any>){
    this.LoginUsers = new LoginUsers();
  }

  ngOnInit(){
    this.view();
  }
  
  view() {
    debugger;
    this.apiCall.DBCalling('View', "Admin", "", "", "", "").subscribe(res => {
      debugger;
      this.dbResult = res;
      this.lstAdmin = this.dbResult[0][0];
    })
  }


  Profile(){
    this.router.navigateByUrl('Profile');
  }

  Logout(){
    debugger;
    this.LoginUsers = new LoginUsers();
    this.store.dispatch(new PageStore.OpenPage(Object.assign({}, this.LoginUsers)));
    this.router.navigateByUrl('Login');
  }

  
}
