import { Component } from '@angular/core';
import { ApicallingService } from '../apicalling.service';
import { NzModalService } from 'ng-zorro-antd/modal';
import { HttpClient } from '@angular/common/http';
import { Router } from '@angular/router';
import { LoginUsers } from '../Store/LoginUsers';
import { Store } from '@ngrx/store';
import * as PageStore from "../Store/PageStore/Page.Actions";

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent {

  imagePath: string = "";
  imageUpload: string = "";
  dbResult: any;

  lstAdmin : any = [];
  Logo : string =""
  restaurant : string = "";
  username : string = "";
  password : string = "";
  submit : boolean = false;
  invalid : string ="";
  LoginUsers : LoginUsers;
  constructor(private apiCall: ApicallingService, private modal: NzModalService, private http: HttpClient,private router:Router,
    private store: Store<any>) {
    this.imagePath = this.apiCall.imagePath;
    this.imageUpload = this.apiCall.ImageUpload;
    this.LoginUsers = new LoginUsers();

  }

  ngOnInit() {
    this.view();
  }

  view() {
    debugger;
    this.apiCall.DBCalling('View', "Admin", "", "", "", "").subscribe(res => {
      debugger;
      this.dbResult = res;
      this.lstAdmin = this.dbResult[0][0];
      this.Logo = this.imagePath + this.lstAdmin.Logo;
      this.restaurant = this.imagePath + this.lstAdmin.Image;
      
    })
  }

  login(){
    debugger;
    this.submit= true;
    if(this.username !='' && this.password !=''){
      debugger;
        if(this.lstAdmin.UserName == this.username && this.lstAdmin.Password == this.password){
          this.LoginUsers.Id = this.lstAdmin.Id;
          this.store.dispatch(new PageStore.OpenPage(Object.assign({}, this.LoginUsers)));
          this.router.navigateByUrl('Dashboard');
        }else{
          this.invalid = "Invalid Credentials!"
        }
    }
  }
}
