import { Component } from '@angular/core';
import { ApicallingService } from '../apicalling.service';
import { NzModalService } from 'ng-zorro-antd/modal';
import { HttpClient } from '@angular/common/http';

@Component({
  selector: 'app-offers',
  templateUrl: './offers.component.html',
  styleUrls: ['./offers.component.css']
})
export class OffersComponent {
  Id : number = 0;
  search:string="";

  imagePath : string ="";
  isVisible:boolean=false;
  Name:string="";
  Description:string="";
  status:boolean=false;
  // imageSrc:string="";
  imageSrc:any;
  submit:boolean=false;
  imageSrc1:string="";
  Image : string ="";

  operation : string = "";
  dbResult : any;
  msg : string = "";
  lstOffers : any = [];
  lstProducts:any=[];
  lstProduct:any=[];
  imageUpload : string=""
  Exist : boolean=false;
  Amount : number = 0;
  lstOfferProducts:any=[];



  constructor(private apiCall : ApicallingService,private modal: NzModalService,private http:HttpClient) { 
    
  }


  ngOnInit(): void {
    debugger;
    this.view('');
    this.viewProducts('');
    this.imagePath = this.apiCall.imagePath;
    this.imageUpload = this.apiCall.ImageUpload;
  }



  expandSet = new Set<number>();
  onExpandChange(id: number, checked: boolean): void {
    debugger;
    this.lstOfferProducts = [];
    if (checked) {
      debugger;
      this.expandSet = new Set<number>();
      this.expandSet.add(id);
      this.apiCall.DBCalling('viewProducts',"Offers","","","",id).subscribe(res=>{
        debugger;
        this.dbResult = res;
        this.lstOfferProducts = this.dbResult[0];
      })
      
    } else {
      this.expandSet.delete(id);
    }
  }

  AddOffer(){
    debugger;
    this.isVisible=true;
  }





  Edit(e:any){
    debugger;
    this.isVisible=true;
    this.Id=e.Id;
    this.Name=e.Name;
    this.Amount=e.Amout;
    this.lstProduct = JSON.parse(e.Products);
    this.Description=e.Description;
    this.status=true;
    this.imageSrc=this.imagePath+e.PostImage;
    this.Image=e.PostImage
  }

  Delete(e:any){
    debugger;
    this.modal.confirm({
      nzTitle: 'Are you sure?',
      nzContent: '<b style="color: red;">You wont be able to revert this!</b>',
      nzOkText: 'Yes',
      nzOkType: 'primary',
      nzOkDanger: true,
      nzOnOk: () => this.apiCall.DBCalling('Delete',"Offers",e,"","","").subscribe(res=>{
        debugger;
        this.dbResult = res;
        if(this.dbResult[0][0].DBresult > 0){
            this.msg = 'Deleted';
            this.success();
            this.isVisible = false;
            this.view('');
        }
      }),
      nzCancelText: 'No',
      nzOnCancel: () => console.log('Cancel')
    });

    
  }

  handleCancel(){
    debugger;
    this.Reset();
    this.isVisible=false;
    this.submit=false;
  }



  saveOffer(){
    debugger;
    this.submit = true;
    if(this.Name !='' && this.Image !='' && this.Amount && this.lstProduct.length !=0){
      debugger;
      var data = ({'Id':this.Id,'Name':this.Name,'Description':this.Description,'PostImage':this.Image,'Type':'','Amount':this.Amount,'Products':this.lstProduct});
      var jsonData =  JSON.stringify(data);
      if(this.Id == 0){
        this.operation = "Insert";
      }else{
        this.operation = "Update";
      }
      this.apiCall.DBCalling(this.operation,"Offers",jsonData,"","","").subscribe(res=>{
        debugger;
        this.dbResult = res;
        if(this.dbResult[0][0].DBresult > 0){
            if(this.Id == 0){
              this.msg = 'Saved';
            }else{
              this.msg = 'Updated';
            }
            this.isVisible = false;
            this.Reset();
            this.success();
            this.view('');
        }else if(this.dbResult[0][0].DBresult == -1){
          debugger;
            this.Exist=true
        }
      });
    }
  }

  success(): void {
    this.modal.success({
      nzTitle: 'Success',
      nzContent: 'Offer ' + this.msg + ' Successfully!'
    });
  }


  view(e:any){
    debugger;
    this.apiCall.DBCalling('View',"Offers","","","",this.search).subscribe(res=>{
      debugger;
      this.dbResult = res;
      this.lstOffers = this.dbResult[0];
    })
  }

  viewProducts(e:any){
    debugger;
    this.apiCall.DBCalling('View',"Products","","","",this.search).subscribe(res=>{
      debugger;
      this.dbResult = res;
      this.lstProducts = this.dbResult[0];
    })
  }
  Reset(){
    this.submit = false;
    this.Id = 0;
    this.Name = "";
    this.Description = "";
    this.Image="";
    this.status=false
  }
  
  onFileChange(event:any){
    debugger;
    this.status = false
    const file = event.target.files[0];
    this.status = event.target.files.length>0?true:false

    var Imageurl= event.target.files[0];
    if(this.status==true){
       const reader = new FileReader();
       reader.readAsDataURL(file);
       reader.onload = () => {
          this.imageSrc = reader.result; 
          this.imageSrc1 = this.imageSrc
          this.saveImage();
       }
    }
  }

  async saveImage(){
     try{
       let value=this.Image;
         this.Image="";
       //  this.loader.loaderon()
         await this.http.post(this.imageUpload, {'image':this.imageSrc}).subscribe((res) => {
             debugger;
             this.dbResult = res;
            if(res==null){
             this.Image=value;
             // this.saveOffer()
            }else{
             debugger;
              this.Image =this.dbResult[0][1];
             //  this.saveOffer()
            }
           })
         }catch(error){
            console.log(error);
       }
   }
}
