import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { LoginComponent } from './login/login.component';
import { MasterPageComponent } from './master-page/master-page.component';
import { CategoriesComponent } from './categories/categories.component';
import { ExtrasComponent } from './extras/extras.component';
import { CouponsComponent } from './coupons/coupons.component';
import { ProductsComponent } from './products/products.component';
import { OffersComponent } from './offers/offers.component';
import { ProfileComponent } from './profile/profile.component';
import { DashboardComponent } from './dashboard/dashboard.component';
import { OrdersComponent } from './orders/orders.component';

const routes: Routes = [
  // { path: '', pathMatch: 'full', redirectTo: '/welcome' },
  // { path: 'welcome', loadChildren: () => import('./pages/welcome/welcome.module').then(m => m.WelcomeModule) }
  {
    path: '',
    pathMatch: 'full',
    redirectTo: 'Login'
  },
  {
    path: '',  component: MasterPageComponent,
    children: [
      {path: '',  component: MasterPageComponent,pathMatch:'full'},
      {path: 'Dashboard',  component:DashboardComponent,pathMatch:'full'},
      {path: 'Categories',  component:CategoriesComponent,pathMatch:'full'},
      {path: 'Extras',  component:ExtrasComponent,pathMatch:'full'},
      {path: 'Coupons',  component:CouponsComponent,pathMatch:'full'},
      {path: 'Products',  component:ProductsComponent,pathMatch:'full'},
      {path: 'Offers',  component:OffersComponent,pathMatch:'full'},
      {path: 'Profile',  component:ProfileComponent,pathMatch:'full'},
      {path: 'Orders',  component:OrdersComponent,pathMatch:'full'},
    ]
  },
  {
    path: 'Login',  component:LoginComponent,pathMatch:'full'
  }
];

@NgModule({
  imports: [RouterModule.forRoot(routes, { useHash: true })],
  exports: [RouterModule]
})
export class AppRoutingModule { }
