import { Component } from '@angular/core';
import { ApicallingService } from '../apicalling.service';
import { NzModalService } from 'ng-zorro-antd/modal';
import { HttpClient } from '@angular/common/http';

@Component({
  selector: 'app-categories',
  templateUrl: './categories.component.html',
  styleUrls: ['./categories.component.css']
})
export class CategoriesComponent {
  Id : number = 0;
  search:string="";
  lstCategory:any=[];
  imagePath : string ="";
  imageUpload : string="";
  isVisible:boolean=false;
  Name:string="";
  Description:string="";
  status:boolean=false;
  // imageSrc:string="";
  imageSrc:any;
  imageSrc1:string="";
  submit:boolean=false;

  Image : string ="";
  IsVegeterian : number = 0;
  operation : string = "";
  dbResult : any;
  msg : string = "";
  lstCategories : any = [];

  Exist : boolean=false;
  constructor(private apiCall : ApicallingService,private modal: NzModalService,private http:HttpClient) { }
  checked = false;
  ngOnInit(): void {
    debugger;
    this.view();
    this.imagePath = this.apiCall.imagePath;
    this.imageUpload = this.apiCall.ImageUpload;
  }

  AddCategory(){
    debugger;
    this.isVisible=true;
  }

  SearchCategory(e:any){
    debugger;
    debugger;
    this.apiCall.DBCalling('View',"categories","","","",this.search).subscribe(res=>{
      debugger;
      this.dbResult = res;
      this.lstCategories = this.dbResult[0];
    })
  }



  Edit(e:any){
    debugger;
    this.isVisible=true;
    this.Id=e.Id;
    this.Name=e.Name;
    this.Description=e.Description;
    this.status=true;
    this.imageSrc=this.imagePath+e.Image;
    this.Image=e.Image
    if(e.IsVegeterian==0){
      this.checked= false;
    }else{
      this.checked= true;
    }
    this.Id=e.Id;
  }

  Delete(e:any){

    this.modal.confirm({
      nzTitle: 'Are you sure?',
      nzContent: '<b style="color: red;">You wont be able to revert this!</b>',
      nzOkText: 'Yes',
      nzOkType: 'primary',
      nzOkDanger: true,
      nzOnOk: () => this.apiCall.DBCalling('Delete',"categories",e,"","","").subscribe(res=>{
        debugger;
        this.dbResult = res;
        if(this.dbResult[0][0].DBresult > 0){
            this.msg = 'Deleted';
            this.success();
            this.isVisible = false;
            this.view();
        }
      }),
      nzCancelText: 'No',
      nzOnCancel: () => console.log('Cancel')
    });

    
  }

  handleCancel(){
    debugger;
    this.Reset();
    this.isVisible=false;
    this.submit=false;
  }

  vegChanges(d:any){
    debugger;
    if(d==true){
      this.IsVegeterian = 1;
    }else{
      this.IsVegeterian = 0
    }
  }

  saveCategory(){
    debugger;
    this.submit = true;
    if(this.Name !='' && this.Image !=''){
      debugger;
      var data = ({'Id':this.Id,'Name':this.Name,'Description':this.Description,'IsVegeterian':this.IsVegeterian,'Image':this.Image});
      var jsonData =  JSON.stringify(data);
      if(this.Id == 0){
        this.operation = "Insert";
      }else{
        this.operation = "Update";
      }
      this.apiCall.DBCalling(this.operation,"categories",jsonData,"","","").subscribe(res=>{
        debugger;
        this.dbResult = res;
        if(this.dbResult[0][0].DBresult > 0){
            if(this.Id == 0){
              this.msg = 'Saved';
            }else{
              this.msg = 'Updated';
            }
            this.isVisible = false;
            this.Reset();
            this.success();
            this.view();
        }else if(this.dbResult[0][0].DBresult == -1){
          debugger;
            this.Exist=true
        }
      });
    }
  }

  success(): void {
    this.modal.success({
      nzTitle: 'Success',
      nzContent: 'Category ' + this.msg + ' Successfully!'
    });
  }


  view(){
    debugger;
    this.apiCall.DBCalling('View',"categories","","","",this.search).subscribe(res=>{
      debugger;
      this.dbResult = res;
      this.lstCategories = this.dbResult[0];
    })
  }

  Reset(){
    this.submit = false;
    this.Id = 0;
    this.Name = "";
    this.Description = "";
    this.Image="";
    this.IsVegeterian = 0;
    this.checked=false;
    this.status=false
  }
  
  onFileChange(event:any){
    debugger;
    this.status = false
    const file = event.target.files[0];
    this.status = event.target.files.length>0?true:false

    var Imageurl= event.target.files[0];
    if(this.status==true){
       const reader = new FileReader();
       reader.readAsDataURL(file);
       reader.onload = () => {
          this.imageSrc = reader.result; 
          this.imageSrc1 = this.imageSrc
          this.saveImage();
       }
    }
  }

  async saveImage(){
     try{
       let value=this.Image;
         this.Image="";
       //  this.loader.loaderon()
         await this.http.post(this.imageUpload, {'image':this.imageSrc}).subscribe((res) => {
             debugger;
             this.dbResult = res;
            if(res==null){
             this.Image=value;
             // this.saveCategory()
            }else{
             debugger;
              this.Image =this.dbResult[0][1];
             //  this.saveCategory()
            }
           })
         }catch(error){
            console.log(error);
       }
   }
}
