import { Component } from '@angular/core';
import { ApicallingService } from '../apicalling.service';
import { NzModalService } from 'ng-zorro-antd/modal';
import { HttpClient } from '@angular/common/http';

@Component({
  selector: 'app-products',
  templateUrl: './products.component.html',
  styleUrls: ['./products.component.css']
})
export class ProductsComponent {

  Id : number = 0;
  search:string="";
  lstProduct:any=[];
  imagePath : string ="";
  isVisible:boolean=false;
  CategoryId:string="";
  Name:string="";
  status:boolean=false;
  // imageSrc:string="";
  imageSrc:any;
  submit:boolean=false;
  imageSrc1:string="";
  Image : string ="";
  Description : number = 0;
  operation : string = "";
  dbResult : any;
  msg : string = "";
  lstCategories : any = [];
  imageUpload : string=""
  Exist : boolean=false;
  Price : number = 0;
  Extras : any = [];
  lstProducts : any = [];
  IsVegeterian : number = 0;
  lstExtras: any = [];
  constructor(private apiCall : ApicallingService,private modal: NzModalService,private http:HttpClient) { }
  checked = false;
  ngOnInit(): void {
    debugger;
    this.view('');
    this.viewCategory();
    this.viewExtras();
    this.imagePath = this.apiCall.imagePath;
    this.imageUpload = this.apiCall.ImageUpload;
  }

  AddProduct(){
    debugger;
    this.isVisible=true;
  }





  Edit(e:any){
    debugger;
    this.isVisible=true;
    this.Id=e.Id;
    this.CategoryId=e.CategoryId;
    this.Name=e.Name;
    this.Price=e.Price;
    this.Extras = JSON.parse(e.Extras);
    this.Description=e.Description;
    this.IsVegeterian=e.IsVegeterian;
    this.status=true;
    this.imageSrc=this.imagePath+e.Image;
    this.Image=e.Image
    if(e.IsVegeterian==0){
      this.checked= false;
    }else{
      this.checked= true;
    }
    this.Id=e.Id;
  }

  Delete(e:any){

    this.modal.confirm({
      nzTitle: 'Are you sure?',
      nzContent: '<b style="color: red;">You wont be able to revert this!</b>',
      nzOkText: 'Yes',
      nzOkType: 'primary',
      nzOkDanger: true,
      nzOnOk: () => this.apiCall.DBCalling('Delete',"Products",e,"","","").subscribe(res=>{
        debugger;
        this.dbResult = res;
        if(this.dbResult[0][0].DBresult > 0){
            this.msg = 'Deleted';
            this.success();
            this.isVisible = false;
            this.view('');
        }
      }),
      nzCancelText: 'No',
      nzOnCancel: () => console.log('Cancel')
    });

    
  }

  handleCancel(){
    debugger;
    this.Reset();
    this.isVisible=false;
    this.submit=false;
  }

  vegChanges(d:any){
    debugger;
    if(d==true){
      this.IsVegeterian = 1;
    }else{
      this.IsVegeterian = 0
    }
  }


  saveProduct(){
    debugger;
    this.submit = true;
    if(this.CategoryId !='' && this.Name!='' && this.Price!=0 && this.Image !=''){
      debugger;
      var data = ({'Id':this.Id,'CategoryId':this.CategoryId,'Name':this.Name,'Description':this.Description,'Price':this.Price,
      'Image':this.Image,'Extras':this.Extras,'IsVegeterian':this.IsVegeterian});
      var jsonData =  JSON.stringify(data);
      if(this.Id == 0){
        this.operation = "Insert";
      }else{
        this.operation = "Update";
      }
      this.apiCall.DBCalling(this.operation,"Products",jsonData,"","","").subscribe(res=>{
        debugger;
        this.dbResult = res;
        if(this.dbResult[0][0].DBresult > 0){
            if(this.Id == 0){
              this.msg = 'Saved';
            }else{
              this.msg = 'Updated';
            }
            this.isVisible = false;
            this.Reset();
            this.success();
            this.view('');
        }else if(this.dbResult[0][0].DBresult == -1){
          debugger;
            this.Exist=true
        }
      });
    }
  }

  viewExtras(){
    debugger;
    this.apiCall.DBCalling('View',"Extras","","","",this.search).subscribe(res=>{
      debugger;
      this.dbResult = res;
      this.lstExtras = this.dbResult[0];
    })
  }
  success(): void {
    this.modal.success({
      nzTitle: 'Success',
      nzContent: 'Product ' + this.msg + ' Successfully!'
    });
  }


  view(e:any){
    debugger;
    this.apiCall.DBCalling('View',"Products","","","",this.search).subscribe(res=>{
      debugger;
      this.dbResult = res;
      this.lstProducts = this.dbResult[0];
    })
  }

  viewCategory(){
    debugger;
    this.apiCall.DBCalling('View',"categories","","","",this.search).subscribe(res=>{
      debugger;
      this.dbResult = res;
      this.lstCategories = this.dbResult[0];
    })
  }

  Reset(){
    this.submit = false;
    this.Id = 0;
    this.CategoryId = "";
    this.Name = "";
    this.Image="";
    this.Description = 0;
    this.checked=false;
    this.status=false
  }
  
  onFileChange(event:any){
    debugger;
    this.status = false
    const file = event.target.files[0];
    this.status = event.target.files.length>0?true:false

    var Imageurl= event.target.files[0];
    if(this.status==true){
       const reader = new FileReader();
       reader.readAsDataURL(file);
       reader.onload = () => {
          this.imageSrc = reader.result; 
          this.imageSrc1 = this.imageSrc
          this.saveImage();
       }
    }
  }

  async saveImage(){
     try{
       let value=this.Image;
         this.Image="";
       //  this.loader.loaderon()
         await this.http.post(this.imageUpload, {'image':this.imageSrc}).subscribe((res) => {
             debugger;
             this.dbResult = res;
            if(res==null){
             this.Image=value;
             // this.saveProduct()
            }else{
             debugger;
              this.Image =this.dbResult[0][1];
             //  this.saveProduct()
            }
           })
         }catch(error){
            console.log(error);
       }
   }

}
