import { Component, OnInit } from '@angular/core';
import { ApicallingService } from '../apicalling.service';
import { NzModalService } from 'ng-zorro-antd/modal';
import { HttpClient } from '@angular/common/http';

@Component({
  selector: 'app-orders',
  templateUrl: './orders.component.html',
  styleUrls: ['./orders.component.css']
})
export class OrdersComponent implements OnInit {
  expandSet = new Set<number>();
  lstOrders :any = [];
  lstOrderDetails:any = [];
  dbResult : any;
  lstAdmin: any = [];
  selectedTab  : string = "All";
  msg : string ="";
  lstOrdersdata : any = [];

  tabs = [
    {name: 'All',icon: 'apple'},
    {name: 'Accepted',icon: 'android'},
    {name: 'Cancelled',icon: 'android'},
    {name: 'Ready for Pick up', icon: 'android' }, 
    {name: 'Picked up', icon: 'android' }, 
    {name: 'Out for Delivery', icon: 'android' }, 
    {name: 'Delivered', icon: 'android' }
  ];

  constructor(private apiCall : ApicallingService,private modal: NzModalService,private http:HttpClient) { }

  ngOnInit(): void {
    debugger;
    this.viewOrders('');
    this.Admin();
  }

  
  onExpandChange(id: any, checked: boolean): void {
    debugger;
    if (checked) {
      this.expandSet = new Set<number>();
      this.expandSet.add(id);
      debugger;
      this.apiCall.DBCalling('orderDetails',"Orders","","","",id).subscribe(res=>{
        debugger;
        this.dbResult = res;
        this.lstOrderDetails = this.dbResult[0];
      })
    } else {
      this.expandSet.delete(id);
    }
  }
  
  viewOrders(search:any){
    debugger;
    this.apiCall.DBCalling('View',"Orders","","","",search).subscribe(res=>{
      debugger;
      this.dbResult = res;
      this.lstOrdersdata = this.dbResult[0];
      this.lstOrders= this.dbResult[0];
    })
  }

  updateOrder(d:string,e:any){
    debugger;
    var data = ({'Id':e.Id,'OrderStatus':d});
    var jsonData =  JSON.stringify(data);

    this.modal.confirm({
      nzTitle: 'Are you sure?',
      nzContent: '<b style="color: red;">You want to '+ d + ' this order</b>',
      nzOkText: 'Yes',
      nzOkType: 'primary',
      nzOkDanger: true,
      nzOnOk: () => this.apiCall.DBCalling('updateOrderStatus',"Orders",jsonData,"","","").subscribe(res=>{
        debugger;
        this.dbResult = res;
        if(this.dbResult[0][0].DBresult > 0){
            this.msg =  d;
            this.success();

            if(d=='Ready for Pick up'){
              this.readyPickup(e);
            }

            if(d=='Picked up'){
              this.PickedUp(e);
            }

            if(d=='Out for Delivery'){
              this.outForDelivery(e);
            }

            if(d=='Delivered'){
              this.delivered(e);
            }
            
            this.viewOrders('');
            this.selectedTab = "All";
        }
      }),
      nzCancelText: 'No',
      nzOnCancel: () => console.log('Cancel')
    });
  }

  success(): void {
    this.modal.success({
      nzTitle: 'Success',
      nzContent: 'Order ' + this.msg + ' Successfully!'
    });
  }

  Admin(){
    this.apiCall.DBCalling('View', "Admin", "", "", "", "").subscribe(res => {
      debugger;
      this.dbResult = res;
      this.lstAdmin = this.dbResult[0][0];
      if(this.lstAdmin.Pickup == 0){
        this.tabs.splice(3,1)
        this.tabs.splice(3,1)
      }
      if(this.lstAdmin.Delivery == 0){
        debugger;
        this.tabs.splice(5,1)
        this.tabs.splice(5,1)
      }
    })
  }


  changeTab(d:string){
    debugger;
    this.selectedTab = d;
    if(d !='All'){
      debugger;
      this.lstOrders = this.lstOrdersdata.filter((x:any)=> x.OrderStatus == d);
    }else if(d =='All'){
      this.lstOrders = this.lstOrdersdata;
    }
  }

  readyPickup(data:any){
    debugger;
    this.http.post('https://nextasoft.in/order-online/Api/pickup.php', 
    {'To':data.Email,'orderId':data.Id,'username':data.customer}
    ).subscribe(res => { 
      debugger;
      console.log(res);
    })
  }

  PickedUp(data:any){
    debugger;
    this.http.post('https://nextasoft.in/order-online/Api/pickup-confirmation.php', 
    {'To':data.Email,'orderId':data.Id,'username':data.customer}
    ).subscribe(res => { 
      debugger;
      console.log(res);
    })
  }

  outForDelivery(data:any){
    debugger;
    this.http.post('https://nextasoft.in/order-online/Api/outfordelivery.php', 
    {'To':data.Email,'orderId':data.Id,'username':data.customer}
    ).subscribe(res => { 
      debugger;
      console.log(res);
    })
  }

  delivered(data:any){
    debugger;
    this.http.post('https://nextasoft.in/order-online/Api/delivered.php', 
    {'To':data.Email,'orderId':data.Id,'username':data.customer}
    ).subscribe(res => { 
      debugger;
      console.log(res);
    })
  }
}
