import { Component } from '@angular/core';
import { ApicallingService } from '../apicalling.service';
import { NzModalService } from 'ng-zorro-antd/modal';
import { HttpClient } from '@angular/common/http';

@Component({
  selector: 'app-coupons',
  templateUrl: './coupons.component.html',
  styleUrls: ['./coupons.component.css']
})
export class CouponsComponent {
  isVisible:boolean=false;
  Name : string="";
  Description : string = "";
  Code : string ="";
  Amount :number = 0;
  Type : string ="Amount";
  dbResult : any;
  search:string="";
  lstCoupons : any= [];
  ValidFrom : string ="";
  ValidTo : string ="";
  constructor(private apiCall : ApicallingService,private modal: NzModalService,private http:HttpClient) { }

  ngOnInit(): void {
    debugger;
    this.view('');
  }

  view(e:any){
    debugger;
    this.apiCall.DBCalling('View',"Coupons","","","",this.search).subscribe(res=>{
      debugger;
      this.dbResult = res;
      this.lstCoupons = this.dbResult[0];
    })
  }

  
  AddCoupons(){
    debugger;
    this.isVisible=true;
  }

  Id : number = 0;
  operation : string ="";
  submit : boolean = false;
  msg : string ="";
  Exist : boolean=false;

  saveCoupons(){
    debugger;
    this.submit = true;
    if(this.Name !='' ){
      debugger;
      var data = ({'Id':this.Id,'Name':this.Name,'Description':this.Description,'Code':this.Code,'Amount':this.Amount,'Type':this.Type
      ,'ValidFrom':this.ValidFrom,'ValidTo':this.ValidTo});
      var jsonData =  JSON.stringify(data);
      if(this.Id == 0){
        this.operation = "Insert";
      }else{
        this.operation = "Update";
      }
      this.apiCall.DBCalling(this.operation,"Coupons",jsonData,"","","").subscribe(res=>{
        debugger;
        this.dbResult = res;
        if(this.dbResult[0][0].DBresult > 0){
            if(this.Id == 0){
              this.msg = 'Saved';
            }else{
              this.msg = 'Updated';
            }
            this.isVisible = false;
            this.Reset();
            this.success();
            this.view('');
        }else if(this.dbResult[0][0].DBresult == -1){
          debugger;
            this.Exist=true
        }
      });
    }
  }

  Edit(e:any){
    debugger;

    
    this.Id = e.Id;
    this.Name = e.Name;
    this.Description = e.Description;
    this.Code = e.Code;
    this.Amount = e.Amount;
    this.Type = e.Type;
    this.ValidFrom = e.ValidFrom;
    this.ValidTo = e.ValidTo;
    this.isVisible = true;
  }

  Delete(e:any){
    this.modal.confirm({
      nzTitle: 'Are you sure?',
      nzContent: '<b style="color: red;">You wont be able to revert this!</b>',
      nzOkText: 'Yes',
      nzOkType: 'primary',
      nzOkDanger: true,
      nzOnOk: () => this.apiCall.DBCalling('Delete',"Coupons",e,"","","").subscribe(res=>{
        debugger;
        this.dbResult = res;
        if(this.dbResult[0][0].DBresult > 0){
            this.msg = 'Deleted';
            this.success();
            this.isVisible = false;
            this.view('');
        }
      }),
      nzCancelText: 'No',
      nzOnCancel: () => console.log('Cancel')
    });

  }


  success(): void {
    this.modal.success({
      nzTitle: 'Success',
      nzContent: 'Coupons ' + this.msg + ' Successfully!'
    });
  }

  Reset(){
    this.Id = 0;
    this.Name = "";
    this.Description = "";
    this.Code = ""
    this.Amount = 0;
    this.Type = ""
    this.submit = false;
  }

  handleCancel(){
    debugger;
    this.Reset();
    this.isVisible=false;
    this.submit=false;
  }
}
